import React, { useState, useCallback, useMemo, useEffect } from 'react'
import { Table, Button, Modal, Form, InputNumber, Select, Input, Upload, notification, Switch, message } from 'antd'
import { CKEditor } from 'ckeditor4-react';
import axios from '../axios';
CKEditor.editorUrl = '/ckeditor/ckeditor.js';
const { Item, useForm } = Form
export default function App() {
    const [Data, setData] = useState([])
    const [Categories, setCategories] = useState([])
    const [form] = useForm()
    const [Visible, setVisible] = useState(false)
    const [EditID, setEditID] = useState('')
    const getData = useCallback(() => {
        axios.get('/news')
            .then(res => {
                setData([...res.data.data])
            })

        axios.get('/categories')
            .then(res => {
                setCategories([...res.data.data])
            })
    }, [])

    const handleEdit = async data => {
        form.setFieldsValue(data)
        setEditID(data._id)
        setVisible(true)
        getData()
    }
    const handleDelete = async _id => {
        const r = window.confirm('Xa?')
        if(!r) return
        await axios.delete(`/news?_id=${_id}`)
        message.success(' xa')
    }
    var columns = [
        {
            title: 'Hnh ng',
            dataIndex: '_id',
            key: '_id',
            render: (_id, data) => <>
                <Button onClick={() => handleEdit(data)}>Sa</Button>
                <Button onClick={() => handleDelete(_id)}>Xa</Button>
            </>
        },
        {
            title: 'Tiu ',
            dataIndex: 'title',
            key: 'title',
        },
        {
            title: 'M t',
            dataIndex: 'meta',
            key: 'meta'
        },
    ];


    useEffect(() => {
        getData()
    }, [getData])

    const handleSubmitForm = useCallback(async (submitData) => {
        const res = await axios.post('/news', submitData)
        message.success(' to danh mc mi')
        getData()
    }, [])

    const handleEditForm = useCallback(async data => {
        const res = await axios.put(`/news?_id=${EditID}`, data)
        message.success(' Sa')
        getData()
    },[EditID])


    return (
        <>
            <div className="container">
                <Form
                    onFinish={handleSubmitForm}
                >
                    <Item label="Tiu " name="title">
                        <Input />
                    </Item>

                    <Item label="M t ngn" name="meta">
                        <Input />
                    </Item>

                    <Item label="nh ba" name="thumbnail">
                        <Input />
                    </Item>
                    <Item label="Loi tin tc" name="type">
                        <Select>
                            <Select.Option value="1" >tin d n</Select.Option>
                            <Select.Option value="2">Tin tc</Select.Option>

                        </Select>
                    </Item>
                    <Item label="Danh mc" name="categories">
                        <Select>
                            {
                                Categories.map(o => <Select.Option key={o._id} value={o._id} >{o.title}</Select.Option>)
                            }

                        </Select>
                    </Item>
                    <Item
                        getValueFromEvent={(data1, data2) => {
                            return data1.editor.getData()
                        }}
                        valuePropName='data' label="Ni dung" name="content">
                        <CKEditor
                            data=""
                        />
                    </Item>

                    <Item>
                        <Button type="primary" htmlType="submit">Thm tin tc</Button>
                    </Item>
                </Form>

                <Table
                    className="components-table-demo-nested"
                    columns={columns}
                    dataSource={Data}
                    scroll={{ x: 1100, y: 600 }}
                    pagination={false}
                />

                <Modal
                    title="Sa Components"
                    visible={Visible}
                    footer={false}
                    onCancel={() => { setVisible(false) }}
                    forceRender
                >
                    <Form
                        form={form}
                        onFinish={handleEditForm}
                    >
                        <Item label="Tiu " name="title">
                            <Input />
                        </Item>

                        <Item label="M t ngn" name="meta">
                            <Input />
                        </Item>

                        <Item label="nh ba" name="thumbnail">
                            <Input />
                        </Item>
                        <Item label="Loi tin tc" name="type">
                            <Select>
                                <Select.Option value="1" >tin d n</Select.Option>
                                <Select.Option value="2">Tin tc</Select.Option>

                            </Select>
                        </Item>
                        <Item label="Danh mc" name="categories">
                            <Select>
                                {
                                    Categories.map(o => <Select.Option key={o._id} value={o._id} >{o.title}</Select.Option>)
                                }

                            </Select>
                        </Item>
                        <Item
                            getValueFromEvent={(data1, data2) => {
                                return data1.editor.getData()
                            }}
                            valuePropName='data' label="Ni dung" name="content">
                            <CKEditor
                                data=""
                            />
                        </Item>

                        <Item>
                            <Button type="primary" htmlType="submit">Sa tin tc</Button>
                        </Item>
                    </Form>
                </Modal>
            </div>

        </>
    )
}