import React, { useState, useCallback, useMemo, useEffect } from 'react'
import { Table, Button, Modal, Form, InputNumber, Select, Input, Upload, notification, Switch, message } from 'antd'
import { CKEditor } from 'ckeditor4-react';
import axios from '../axios';
CKEditor.editorUrl = '/ckeditor/ckeditor.js';
const { Item, useForm } = Form
export default function App() {
    const [Data, setData] = useState([])

    const [form] = useForm()
    const [Visible, setVisible] = useState(false)
    const [EditID, setEditID] = useState('')
    const getData = useCallback(() => {
        axios.get('/categories')
            .then(res => {
                setData([...res.data.data])
            })
    }, [])
    const handleEdit = data => {
        form.setFieldsValue(data)
        setEditID(data._id)
        setVisible(true)
        getData()
    }
    const handleDelete = async _id => {
        const r = window.confirm('Xa?')
        if(!r) return
        await axios.delete(`/categories?_id=${_id}`)
        message.success(' xa')
    }
    var columns = [
        {
            title: 'Hnh ng',
            dataIndex: '_id',
            key: '_id',
            render: (_id, data) => <>
                <Button onClick={() => handleEdit(data)}>Sa</Button>
                <Button onClick={() => handleDelete(_id)}>Xa</Button>
            </>
        },
        {
            title: 'Tiu ',
            dataIndex: 'title',
            key: 'title',
        },
        {
            title: 'M t',
            dataIndex: 'meta',
            key: 'meta'
        },
    ];

    

    useEffect(() => {
        getData()
    }, [getData])

    const handleSubmitForm = useCallback(async (submitData) => {
        const res = await axios.post('/categories', submitData)
        message.success(' to danh mc mi')
        getData()
    }, [])

    const handleEditForm = useCallback(async data => {
        console.log(data);
        const res = await axios.put(`/categories?_id=${EditID}`, data)
        message.success(' Sa')
        getData()
    }, [EditID])
    return (
        <>
            <div className="container">
                <Form
                    onFinish={handleSubmitForm}
                >
                    <Item label="Tiu " name="title">
                        <Input />
                    </Item>

                    <Item label="M t ngn" name="meta">
                        <Input />
                    </Item>

                    <Item label="nh ba" name="thumbnail">
                        <Input />
                    </Item>

                    <Item label="Loi danh mc" name="type">
                        <Select>
                            <Select.Option value="1" >D n</Select.Option>
                            <Select.Option value="2">Tin tc</Select.Option>

                        </Select>
                    </Item>
                    <Item>
                        <Button type="primary" htmlType="submit">Thm danh mc</Button>
                    </Item>
                </Form>

                <Table
                    className="components-table-demo-nested"
                    columns={columns}
                    dataSource={Data}
                    scroll={{ x: 1100, y: 600 }}
                    pagination={false}
                />
                <Modal
                    title="Sa Components"
                    visible={Visible}
                    footer={false}
                    onCancel={() => { setVisible(false) }}
                    forceRender
                >
                    <Form
                        form={form}
                        onFinish={handleEditForm}
                    >
                        <Item label="Tiu " name="title">
                            <Input />
                        </Item>

                        <Item label="M t ngn" name="meta">
                            <Input />
                        </Item>

                        <Item label="nh ba" name="thumbnail">
                            <Input />
                        </Item>

                        <Item label="Loi danh mc" name="type">
                            <Select>
                                <Select.Option value="1" >D n</Select.Option>
                                <Select.Option value="2">Tin tc</Select.Option>

                            </Select>
                        </Item>
                        <Item>
                            <Button type="primary" htmlType="submit">Thm danh mc</Button>
                        </Item>
                    </Form>
                </Modal>
            </div>

        </>
    )
}